import { connect } from "react-redux"
import { loadToys } from "../store/actions/toyActions"
import React from "react"
import { ToyList } from "../cmps/ToyList"
import { Link } from "react-router-dom"
import { ToyFilter } from "../cmps/ToyFilter"


class _ToyApp extends React.Component{

    state = {
    }

    componentDidMount() {
        this.props.loadToys(this.props.currFilter)
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.currFilter !== this.props.currFilter) this.props.loadToys(this.props.currFilter)
    }
    
    

    render(){
        const toys = this.props.toys
        if (!toys) return  <h1>Loading...</h1>
        return(
            <main>
                <ToyFilter somename={this.props.currFilter}/>
                <Link to="toy/edit">Add Toy</Link>
                <ToyList toys={toys}/>
            </main>
        )
    }
}

const mapGlobalStateToProps = (state) => {
    return {
        toys: state.toyReducer.toys,
        currFilter: state.toyReducer.currFilter
      
    }
}

const mapDispatchToProps= {
    loadToys
}

export const ToyApp = connect(mapGlobalStateToProps, mapDispatchToProps)(_ToyApp)
