import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ExitToAppOutlinedIcon from '@material-ui/icons/ExitToAppOutlined';
import { logout } from "../store/actions/userActions"
import { connect } from 'react-redux';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
}));

export function _DenseAppBar(props) {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar variant="dense">
          <IconButton onClick={props.toggleSideBar} edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
            <MenuIcon/>
          </IconButton>
          <Typography className="header-txt flex s-b" variant="h6" color="inherit">
            <span>Mister-Toy's Shop</span> 
            <span onClick={props.logout} className="flex center">{<ExitToAppOutlinedIcon/>} Logout</span>
          </Typography>
        </Toolbar>
      </AppBar>
    </div>
  );
}


const mapGlobalStateToProps = (state) => {
  return {}
}

const mapDispatchToProps= {
  logout
}

export const DenseAppBar = connect(mapGlobalStateToProps, mapDispatchToProps)(_DenseAppBar)
