import { connect } from "react-redux"
import { setFilter } from "../store/actions/toyActions"
import React from "react"
import { Formik, Form, Field, ErrorMessage } from 'formik';


class _ToyFilter extends React.Component{

    componentDidMount() {
        
    }
    
    
    render(){
        const {currFilter} = this.props
        return(
            <div className="filter-form flex col">
            <Formik
                initialValues={currFilter}
                onInputChange ={(values)=> this.props.setFilter(values)}
                onSubmit={(values, { setSubmitting }) => {
                    this.props.setFilter(values)
                    setTimeout(() => {
                        setSubmitting(false);
                    }, 400)
                }}
            >
              {({ isSubmitting }) => (
                <Form className="flex col-st">
                  <h1>Search:</h1>
                  <Field type="text" name="name" placeholder="Toy Name" />
                  <ErrorMessage name="name" component="div" />
                  <Field type="text" name="type" placeholder="Type"/>
                  <ErrorMessage name="type" component="div" />
                  <div>
                    <br/>
                    <span>Price: </span>
                    <br />
                  <Field type="number" name="minPrice" placeholder="From:"/>
                  <ErrorMessage name="minPrice" component="div" />
                  <Field type="number" name="maxPrice" placeholder="to:"/>
                  <ErrorMessage name="maxPrice" component="div" />
                  </div>
                  <label>
                    <br/>
                  In Stock: 
                  <br/>
                  <br/>
                  <Field as="select" name="inStock">
                  <option value="all">All</option>
                  <option value="inStock">In Stock</option>
                  <option value="notInStock">Not In Stock</option>
                  </Field>
                  </label>

                  <button type="submit" disabled={isSubmitting}>
                    Search
                  </button>
                </Form>
              )}
            </Formik>
          </div>
        )
    }
}

const mapGlobalStateToProps = (state) => {
    return {
        currFilter: state.toyReducer.currFilter
    }
}

const mapDispatchToProps= {
    setFilter
}

export const ToyFilter = connect(mapGlobalStateToProps, mapDispatchToProps)(_ToyFilter)
