import { userService } from '../../services/userService.js'

export function loggin(userToLogin) {
    return async dispatch => {
        try {
            const user = await userService.loggin(userToLogin)
            dispatch({ type: 'LOGIN', user })
            dispatch({ type: 'SHOW_MSG', msg: 'Logged In Successfully' })
            setTimeout(dispatch, 1000, {type: 'CLEAR_MSG'})
            return user
        } catch (err) {
            dispatch({ type: 'SHOW_MSG', msg: 'Proplems Loging In  Plaese Try Again!' })
            setTimeout(dispatch, 1000, {type: 'CLEAR_MSG'})
        }
    }
}

export function signup(userToSignup) {
    return async dispatch => {
        try {
            const user = await userService.signup(userToSignup)
            dispatch({ type: 'LOGIN', user })
            setTimeout(dispatch, 1000, {type: 'CLEAR_MSG'})
            dispatch({ type: 'SHOW_MSG', msg: 'Signed In Successfully' })
        } catch (err) {
            dispatch({ type: 'SHOW_MSG', msg: 'Proplems Signing Up  Plaese Try Again!' })
            setTimeout(dispatch, 1000, {type: 'CLEAR_MSG'})
        }
    }
}

export function logout() {
    return async dispatch => {
        try {
            const user = await userService.logout()
            dispatch({ type: 'LOGOUT', user })
            dispatch({ type: 'SHOW_MSG', msg: 'Logged-Out Successfully' })
            setTimeout(dispatch, 1000, {type: 'CLEAR_MSG'})

        } catch (err) {
            dispatch({ type: 'SHOW_MSG', msg: 'Proplems Logging-Out  Plaese Try Again!' })
            setTimeout(dispatch, 1000, {type: 'CLEAR_MSG'})
        }
    }
}