import { toyService } from '../../services/toyService.js'

export function removeToy(toyId) {
    return dispatch => {
        toyService.remove(toyId)
        .then(toy => dispatch({ type: 'REMOVE_TOY', toyId }))
        
    }
}

export function loadToys(filter) {
    return dispatch => {
        toyService.query(filter)
            .then(toys => dispatch({ type: 'LOAD_TOYS', toys }))
    }
}

export function updateToy(toy) {
    return dispatch => {
        toyService.update(toy)
        .then(toy => dispatch({ type: 'UPDATE_TOY', toy }))
        
    }
}

export function showMsg(msg) {
    return dispatch => {
        dispatch({ type: 'SHOW_MSG', msg })
    }
}
export function clearMsg() {
    return dispatch => {
        dispatch({ type: 'CLEAR_MSG'})
    }
}

export function setFilter(filter){
    return dispatch => dispatch({ type: 'SET_FILTER', filter })
}

