import {Component} from "react"
import { GoogleMap } from "../cmps/GoogleMap";
export class About extends Component{
    
    state ={
        pos: {lat: 32.080033, lng: 34.775499}
    }

    onCenterMap = (pos) => {
        this.setState({pos})
    }

    render(){
        const {pos} = this.state
        return (
            <div>
                <h1>About</h1>
                <button onClick={()=> this.onCenterMap({lat: 32.189333,lng: 34.856499})}>Tel Aviv</button>
                <button onClick={()=> this.onCenterMap({lat:32.44192, lng:34.9039})}>Hadera</button>
                <button onClick={()=> this.onCenterMap({lat:32.017136,lng:34.745441})}>Bat Yam</button>
                <GoogleMap pos={pos} className="about-Page-map"/>
               
            </div>
        )
    }
}