import { httpService } from './httpService.js'

const baseUrl = 'toy'

export const toyService = {
    query,
    getById,
    remove,
    update
}


function query(filter) {
    var strParams = '?'
    for (var param in filter){
        if (filter[param]) strParams+= `&${param}=${filter[param]}`
    } 
    return httpService.get(baseUrl+strParams)
}


function getById(id) {
    return httpService.get(`${baseUrl}/${id}`)
}

function remove(id) {
    return httpService.delete(`${baseUrl}/${id}`)
}

function update(toy) {
    if (toy._id) {
        return httpService.put(`${baseUrl}/${toy._id}`, toy)
    } else {
        return httpService.post(baseUrl, toy)
    }
}
