import { httpService } from "./httpService.js"

const baseUrl = 'review'
export const reviewService = {
    query,
    add
}

async function query(filterBy) {
    var strParams = '?'
    for (const key in filterBy){
        if (filterBy[key]) strParams+= `&${key}=${filterBy[key]}` 
    }
    try {
        const reviews = await httpService.get(baseUrl+strParams)
        return reviews
    } catch(err){
        console.log(err);
    }
}

async function add(reviewToAdd) {
    try {
        const review = await httpService.post(baseUrl, reviewToAdd)
        return review
    } catch (err) {
        console.log(err)
    }

}