import React from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom';
import { ReviewList } from '../cmps/ReviewList.jsx';
import { reviewService } from '../services/reviewService.js';
import { toyService } from '../services/toyService.js'
import { addReview } from '../store/actions/reviewActions.js'

class _ToyDetails extends React.Component {

    state = {
        toy: null,
        reviewToAdd: '',
        reviews: []
    }

    async componentDidMount() {
        const toyId = this.props.match.params.toyId
        const toy = await toyService.getById(toyId)
        this.setState({ toy })
        const reviews = await reviewService.query({ aboutToyId: toyId })
        this.setState({ reviews })
    }

     onAddReview = async(ev) => {
        ev.preventDefault()
        if (!this.props.loggedUser) return
        const review = {
            txt: this.state.reviewToAdd,
            aboutToyId: this.state.toy._id
        }
        try{
            await this.props.addReview(review)
            this.setState({reviewToAdd: ''})
            const reviews = await reviewService.query({ aboutToyId: this.state.toy._id })
            this.setState({ reviews })
        } catch(err) {
            console.log(err)
        }

    }

    render() {
        const { toy } = this.state
        const { loggedUser } = this.props
        if (!toy) return <h1>Loading...</h1>
        return (
            <main className="toy-details">
                {toy.url && <img src={toy.url}/>}
                <h2>{toy.name}</h2>
                <h3>Price: ${toy.price}</h3>
                <h3>Category: {toy.type}</h3>
                <h4>{toy.inStock ? 'In Stock' : 'Currently out of stock'}</h4>
                {loggedUser?.isAdmin && <Link to={`/toy/edit/${toy._id}`}>Edit</Link>}
                <hr />
                <form onSubmit={this.onAddReview}>
                    <h3>Add Review:</h3>
                    <input onChange={({ target }) => this.setState({ reviewToAdd: target.value })} type="text"  value={this.state.reviewToAdd} placeholder="Your review" />
                    <button>Add</button>
                </form>
                <hr />
                <ReviewList reviews={this.state.reviews} />
            </main>
        )
    }

}


const mapGlobalStateToProps = (state) => {
    return {
        toys: state.toyReducer.toys,
        loggedUser: state.userReducer.loggedUser
    }
}

const mapDispatchToProps = {
    addReview
}
export const ToyDetails = connect(mapGlobalStateToProps, mapDispatchToProps)(_ToyDetails)