import { reviewService } from '../../services/reviewService.js'

export function loadReviews(toyId) {
    return async dispatch => {
        try {
            const reviews = await reviewService.query(toyId)
            dispatch({ type: 'SET_REVIEWS', reviews })
            return reviews
        } catch (err) {
            console.log(err)
        }

    }
}

export function addReview(reviewToAdd) {
    return async dispatch => {
        try {
            const review = await reviewService.add(reviewToAdd)
            dispatch({ type: 'ADD_REVIEW', review })
            setTimeout(dispatch, 1000, {type: 'CLEAR_MSG'})
            dispatch({ type: 'SHOW_MSG', msg: 'review was added' })
            return review
        } catch (err) {
            console.log(err)
        }
    }
}