import { Route, Switch } from 'react-router-dom';
import Home from './pages/Home';
import { ToyApp } from './pages/ToyApp';
import { ToyDetails } from './pages/ToyDetails';
import { ToyEdit } from './pages/ToyEdit';
import { About } from './pages/about';
import { DenseAppBar } from './cmps/DenseAppBar';
import { SideBar } from './cmps/SideBar';
import { Component } from 'react';
import './scss/styles.scss';
import { Footer } from './cmps/Footer';
import { Login } from './pages/Login';
import {Signup} from './pages/Signup';
import { UserMsg } from './cmps/UserMsg';


export class App extends Component {

  state = {
    isSideBarOpen: false
  }
  toggleSideBar = () => {
    this.setState({ isSideBarOpen: !this.state.isSideBarOpen })
  }
  render() {

    return (
      <main className="app-container">
        <header className="main-header">
          <DenseAppBar toggleSideBar={this.toggleSideBar} />
          <UserMsg/>
        </header>
        <aside className="main-side-bar">
          <SideBar isOpen={this.state.isSideBarOpen} />
        </aside>
        <div className="main-app-container flex">
          <Switch>
            <Route path="/toy/edit/:toyId?" component={ToyEdit} />
            <Route path="/toy/:toyId" component={ToyDetails} />
            <Route path="/toy" component={ToyApp} />
            <Route path="/about" component={About} />
            <Route path="/login" component={Login} />
            <Route path="/signup" component={Signup} />
            <Route path="/" component={Home} />
          </Switch>
        </div>
        <Footer />
      </main>
    )
  }
}

