const initialState = {
    toys: [],
    userMsg: null,
    currFilter: {
        name: '',
        type: '',
        minPrice: undefined,
        maxPrice: undefined,
        inStock: undefined
    }
}

export function toyReducer(state = initialState, action) {
    switch (action.type) {
        case 'SHOW_MSG':
            return {...state, userMsg: action.msg }
        case 'CLEAR_MSG':
            return {...state, userMsg: '' }
        case 'LOAD_TOYS':
            return {...state, toys: action.toys }
        case 'UPDATE_TOY':
            const toyIdx = state.toys.findIndex(toy => toy._id === action.toy._id)
            const toys = [...state.toys]
            if (toyIdx >= 0) toys.splice(toyIdx, 1, action.toy)
            else var newToys = [...toys, action.toy]
            return {...state, toys: newToys || toys }
        case 'REMOVE_TOY':
            return {...state, toys: state.toys.filter(toy => toy._id !== action.toyId) }
        case 'SET_FILTER':
            return {...state, currFilter: action.filter }
        default:
            return state
    }
}