import { connect } from 'react-redux'

function _UserMsg(props){
    return props.userMsg ? <h1 className="user-msg flex j-center">{props.userMsg}</h1> : null
}

const mapGlobalStateToProps = (state) => {
    return {
        userMsg: state.toyReducer.userMsg 
    }
}

export const UserMsg = connect(mapGlobalStateToProps)(_UserMsg)
