import React from 'react'
import { connect } from 'react-redux'
import {updateToy, removeToy} from '../store/actions/toyActions.js'
import {toyService} from '../services/toyService.js'
import Uploader from '../cmps/Uploader.jsx'

class _ToyEdit extends React.Component{

    state = {
        toy:{
            name: '',
            price: undefined,
            type: '',
            inStock: true
        }
    }
    
    componentDidMount() {
        const {toyId} = this.props.match.params
        if(toyId){
            toyService.getById(toyId)
              .then(toy => this.setState({toy}))
        }
    }

    handleChange = (ev) => {
            var {name, value, type, checked} = ev.target
            value = (+value) ? +value : value
            if (type === 'checkbox') value = checked
            const {toy} = this.state
            this.setState({toy: {...toy, [name]: value }})
    }

    saveChanges = (ev) => {
        ev.preventDefault()
        console.log('updating', this.state.toy);
        this.props.updateToy(this.state.toy)
        this.props.history.push('/toy')
    }

    onDelete = () => {
        const {toy} = this.state
        this.props.removeToy(toy._id)
        this.props.history.push('/toy')
    }
    onUploadToyImage = (url) => {
        this.setState(prevState => ({toy: {...prevState.toy, url}}))
    }
    
    render(){
        const {toy} = this.state
        const isAdd = this.props.match.params.toyId
        return(
            <main className="toy-edit">
                <form className="flex col" onSubmit={this.saveChanges}>
                    <input onChange={this.handleChange} name="name" type="text" value={toy.name} placeholder="Name"/>
                    <input onChange={this.handleChange} name="type" type="text" value={toy.type} placeholder="Type"/>
                    <input onChange={this.handleChange} name="price" type="number" value={toy.price} placeholder="Price"/>
                    <label><input onChange={this.handleChange} name="inStock" type="checkbox" checked={toy.inStock}/>In Stock</label>
                    <Uploader onFinishUpload={ this.onUploadToyImage } />
                    <button>Save</button>
                {isAdd && <button type="button" onClick={this.onDelete}>Delete</button>}
                </form>
            </main>
        )
    }
    
}


const mapGlobalStateToProps = (state) => {
    return {
        toys: state.toyReducer.toys
    }
}

const mapDispatchToProps = {
    updateToy,
    removeToy
}

export const ToyEdit = connect(mapGlobalStateToProps, mapDispatchToProps)(_ToyEdit)